<script>
import { defineComponent } from "vue";
import { Bar } from "vue3-chart-v2";

export default defineComponent({
  name: "MonthlyChart",
  extends: Bar,

  mounted() {
    this.renderChart({
      labels: [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ],
      datasets: [
        {
          label: "Portfolio",
          backgroundColor: "blue",
          height: 1,
          data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11],
        },
        {
          label: "Dollar",
          backgroundColor: "#42e6f5",
          height: 1,
          data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11],
        },
        {
          label: "ETFs",
          backgroundColor: "red",
          height: 1,
          data: [40, 20, 12, 39, 10, 40, 39, 80, 40, 20, 12, 11],
        },
      ],

      options: {
        scales: {
          yAxes: [
            {
              ticks: {
                stacked: true,
                beginAtZero: true,
              },
            },
          ],
          xAxes: [
            {
              ticks: {
                stacked: true,
              },
            },
          ],
        },
        responsive: true,
      },
    });
  },
});
</script>
