<script>
import { defineComponent } from "vue";
import { Doughnut } from "vue3-chart-v2";

export default defineComponent({
  name: "MonthlyChart",
  extends: Doughnut,

  mounted() {
    this.renderChart({
      labels: [],
      datasets: [
        {
          label: "07 INDUSTRIES",
          backgroundColor: ["blue", "green", "yellow", "purple", "red", "grey"],
          height: 1,
          data: [57, 50, 50, 20, 15, 8],
        },
      ],

      options: {
        scales: {
          yAxes: [
            {
              ticks: {
                stacked: true,
                beginAtZero: true,
              },
            },
          ],
          xAxes: [
            {
              ticks: {
                stacked: true,
              },
            },
          ],
        },
        responsive: true,
      },
    });
  },
});
</script>
